<template>
    <div v-if="posts.length > 0">
        <h3>Список постов</h3>
        <transition-group name="list">
            <post-item @remove="$emit('remove',post)" class="post" v-for="post in posts" :post="post" :key="post.id"/>
        </transition-group>
    </div>
    <h2 v-else style="color: red">Список постов пуст</h2>
</template>

<script>
import PostItem from '@/components/PostItem.vue';    
export default {
    components: {
        PostItem
    },
    props: {
        posts: {
            type: Array,
            required: true
        }
    }
}
</script>

<style scoped>
.list-enter-active,
.list-leave-active {
    transition: all .5s ease;
}

.list-enter-from,
.list-leave-to {
    opacity: 0;
    transform: translateX(100%);
}

.list-move{
    transition: transform .8s ease;
}
</style>