<template>
    <div>
        <h1>Страница с постами</h1>
        <my-input @update:model-value="setSearchQuery" v-focus v-model="searchQuery" placeholder="Поиск поста..."/>
        <div class="app__btns">
            <my-button @click="showDialog">Создать пост</my-button>
            <my-select @update:model-value="setSelectedSort" v-model="selectedSort" :options="sortOptions" />
        </div>
        <my-dialog v-model:show="dialogVisible">
            <post-form @create="createPost" />
        </my-dialog>
        <post-list v-if="!isPostsLoading" :posts="sortedAndSearchedPosts" @remove="removePost" />
        <div v-else>Идет загрузка...</div>
        <div v-intersection="loadMorePosts" class="observer"></div>
    </div>
</template>

<script>
import PostForm from "@/components/PostForm.vue";
import PostList from "@/components/PostList.vue";
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'

export default ({
    components: {
        PostForm,
        PostList
    },
    data() {
        return {
            dialogVisible: false
        }
    },
    methods: {
        ...mapMutations({
            setPage: 'post/setPage',
            setSearchQuery: 'post/setSearchQuery',
            setSelectedSort: 'post/setSelectedSort',
        }),
        ...mapActions({
            loadMorePosts: 'post/loadMorePosts',
            fetchPosts: 'post/fetchPosts'
        }),
        createPost(post) {
            this.posts.push(post)
            this.dialogVisible = false
        },
        removePost(post) {
            this.posts = this.posts.filter(p => p.id !== post.id)
        },
        showDialog() {
            this.dialogVisible = true
        },
        
        /* /*  changePage(pageNumber){
             this.page = pageNumber
         }  */
    },
    mounted() {
        this.fetchPosts()
    },
    computed: {
        ...mapState({
            posts: state => state.post.posts,
            isPostsLoading: state => state.post.isPostsLoading,
            selectedSort: state => state.post.selectedSort,
            searchQuery: state => state.post.searchQuery,
            page: state => state.post.page,
            limit: state => state.post.limit,
            totalPages: state => state.post.totalPages,
            sortOptions: state => state.post.sortOptions
        }),
        ...mapGetters({
            sortedPosts: 'post/sortedPosts',
            sortedAndSearchedPosts: 'post/sortedAndSearchedPosts'
        })
    },
    watch: {
        /*  page(){
             this.fetchPosts()
         } */
    }
})
</script>

<style>
.app__btns {
    display: flex;
    justify-content: space-between;
    margin: 15px 0px;
}

.page__wrapper {
    display: flex;
    margin: 15px;
    justify-content: space-between;
    max-width: 600px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

.page {
    border: 1px solid black;
    padding: 10px;
}

.current-page {
    border: 2px solid teal;
}

.observer {
    height: 30px;
}
</style>